<template>
  <form @submit="onSubmit" class="add-form">
    <div class="form-control">
      <label>Add Name</label>
      <input type="text" v-model="text" name="text" placeholder="--Name--" />
    </div>
    <div class="form-control">
      <label>Add image link</label>
      <input type="text" v-model="image" name="image" placeholder="--Link--" />
    </div>
    <div class="form-control">
      <label>Price</label>
      <input type="text" v-model="price" name="price" placeholder="--199--" />
    </div>

    <input type="submit" value="Save" class="btn btn-block" />
  </form>
</template>

<script>
export default {
  name: "AddTask",
  data() {
    return {
      text: "",
      image: "",
      price: "",
    };
  },
  methods: {
      onSubmit(e){
          e.preventDefault()

          if(!this.text) {
              alert('Plase add a task')
              return
          }
          const newTask={
              id: Math.floor(Math.random()*100000),
              text: this.text,
              image: this.image,
              price: this.price,
          }
          this.$emit('add-task', newTask)
      }
  }
};
</script>

<style scoped>
.add-form {
  margin-bottom: 40px;
}
.form-control {
  margin: 20px 0;
}
.form-control label {
  display: block;
}
.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}
.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.form-control-check label {
  flex: 1;
}
.form-control-check input {
  flex: 2;
  height: 20px;
}
</style>