<template>
  <div class="container">
    <Header
      @toggle="toggleAdd"
      title="Mike's store"
      :showAddTask="showAddTask"
    />
    <div v-show="showAddTask">
      <AddTask @add-task="addTask" />
    </div>

    <Tasks @delete-task="deleteTask" :tasks="tasks" />
  </div>
</template>

<script>
import Header from "./components/Header";
import Tasks from "./components/Tasks";
import AddTask from "./components/AddTask";

export default {
  name: "App",
  components: {
    Header,
    Tasks,
    AddTask,
  },
  data() {
    return {
      tasks: [],
      showAddTask: false,
    };
  },
  methods: {
    toggleAdd() {
      this.showAddTask = !this.showAddTask;
    },
    addTask(task) {
      this.tasks = [...this.tasks, task];
    },
    deleteTask(id) {
      if (confirm("Are you sure??")) {
        this.tasks = this.tasks.filter((task) => task.id !== id);
      }
    },
  },
  created() {
    this.tasks = [
      {
        id: "1",
        text: "Thermal Fleece Jacket",
        price: "200",
        image:
          "https://hplussport.com/wp-content/uploads/2016/12/slicker-jacket_LYNDA_29941.jpg",
        reminder: true,
      },
      {
        id: "2",
        text: "Jean",
        price: "150",
        image:
          "https://hplussport.com/wp-content/uploads/2016/12/unisex-grunge-jeans_LYNDA_29937.jpg",
        reminder: true,
      },
      {
        id: "3",
        text: "Blue Jacket",
        price: "220",
        image:
          "https://hplussport.com/wp-content/uploads/2016/12/ski-coat_LYNDA_29940.jpg",
        reminder: false,
      },
    ];
  },
};
</script>

