<template>
  <div class="task">
    <h3>
      {{ task.text }}
      <i @click="onDelete(task.id)" class="fas fa-times"></i>
    </h3>
    <img class="img-fluid d-block" :src="task.image" />
    <h4>detail</h4>
    <p>price {{ task.price }}</p>
    <ButtonToCart text="Add to cart" color="green" />
  </div>
</template>

<script>
import ButtonToCart from "./Button_to_cart";
export default {
  name: "Task",
  props: {
    task: Object,
  },
  components: {
    ButtonToCart,
  },
  methods: {
    onDelete(id) {
      this.$emit('delete-task', id)
    },
  },
};
</script>

